@import url(https://fonts.googleapis.com/css2?family=Gothic+A1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gothic+A1&display=swap);
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.link {
    text-decoration: none;
}

.start__image {
    width: 10vh;
}

.fade-in {
    animation: fade-in 3s;
    -moz-animation: fade-in 3s;
    -o-animation: fade-in 3s;
}

.fade-out {
    animation: fade-out 3s;
    -moz-animation: fade-out 3s;
    -o-animation: fade-out 3s;
}
    
@keyframes fade-in {
    0% {opacity: 0.2} 
    100% {opacity: 1}
}
    
@keyframes fade-out {
    0% {opacity: 0.2} 
    100% {opacity: 1}
}
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #d5c7e8;
}
.image {
    width: 40vh;
    height: 40vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.game__image {
    width: 100%;
    border: 1.5px solid black;
}

.ending__image {
    width: 10vh;
}
.quest {
    font-family: 'Gothic A1', sans-serif;
    width: 40vh;
    height: 20%;
    border: 1.5px solid black;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    margin: 20px;
    letter-spacing: -1.5px;
    font-weight: 600;
    background-color: #ededed;
}

span {
    margin: 10px;
    margin-top: 20px;
}

button {
    font-family: 'Gothic A1', sans-serif;
    letter-spacing: -1px;
    background-color: #ededed;
    margin-top: 20px;
    border: 0;
    outline: 0;
    margin: 5px;
    font-size: 0.8em;
}

button:nth-child(3) {
    margin-bottom: 20px;
}
.letter {
    font-family: 'Gothic A1', sans-serif;
    margin: 30px;
    letter-spacing: -1.5px;
    font-weight: 600;
    font-size: 1em;
    line-height: 1.7;
}
